.wp-block-create-block-atc-solution-grid {
	margin-bottom: 0;
}

.atc-sol-card {
	background-color: #0f141f;
	padding: 12px;

	.atc-sol-card-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.atc-sol-icon {
		padding: 10px;
		background: #1b202b;
		display: inline-block;
		img {
			width: 39px;
			height: 39px;
		}
	}
	.atc-sol-title {
		h4 {
			color: #fafcff;
			font-size: 20px;
			font-weight: 700;
			line-height: 125%;
			display: inline-block;
		}
	}
	.atc-sol-text {
		font-size: 16px;
		line-height: 141%;
		font-weight: normal;
		color: #aab0bc;
		padding-top: 20px;
		display: none;
	}
	.atc-sol-switch {
		cursor: pointer;
		i {
			color: #fafcff;
			font-size: 20px;
		}
	}
}
